* {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    background: radial-gradient(circle, skyblue, steelblue);
    color: #222;
}

img,
canvas {
    display: block;
    max-width: 100%;
}

.btn {
    /* position: relative; */
    display: flex;
    align-items: center;
    justify-content: center;
    border: 2px solid #222;
    border-radius: 6px;
    cursor: pointer;
    padding: 12px;
    background-color: white;
    user-select: none;
    transition: opacity ease-in .25s;
}

.btn:hover {
    opacity: 0.8;
}

/* .app__input-mask {
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    width: 100%;
} */

.app__wrap {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.app__source-wrap {
    margin-top: 120px;
    width: 300px;
    aspect-ratio: 1;
    position: relative;
}

.app__input-wrap {
    aspect-ratio: 1;
    display: none;
    align-items: center;
    justify-content: center;
    border-radius: 24px;
    border: 6px solid #222;
    cursor: pointer;
    transition: opacity ease-in .25s;
}

.app__input-wrap:hover,
.dragenter {
    opacity: 0.4;
}



.app__input-label {
    margin: 0;
    padding: 0;
    font-weight: bold;
    font-size: 180px;
    text-align: center;
    /* user-select: none; */
    z-index: -100;
    /* display: none; */
}

.app__input-open {
    display: none;
}

.app__img {
    display: none;
}

.app__btn-close {
    display: none;
    position: absolute;
    top: -80px;
    right: -80px;
    width: 60px;
    aspect-ratio: 1;
    font-size: 20px;
}

.app__btn-save {
    display: none;
    position: absolute;
    top: -80px;
    left: 50%;
    transform: translateX(-50%);
    font-size: 20px;
    height: 60px;
    text-decoration: none;
    color: #222;
}

.app__result-wrap {
    display: none;
    margin-top: 120px;
    width: 300px;
}

.show {
    display: flex;
}